/**
 * Transforms a key input into an array of objects for the keybinding API
 *
 * @param {string} key - A key string
 * @returns {{"key": string}[]} Keybinding objects
 */
const getLeftRight = (key) => [`${key}Left`, `${key}Right`].map((k) => ({ key: k }));

const SHIFT_KEYS = getLeftRight("Shift");
const CTRL_KEYS = getLeftRight("Control");

/**
 * Registers the system's keybindings
 */
export const registerSystemControls = () => {
  game.keybindings.register("ffd20", "skipConfirmPrompt", {
    name: "FFD20.KEYBINDINGS.SkipConfirmPrompt.Name",
    uneditable: SHIFT_KEYS,
    onDown: () => {
      ffd20.skipConfirmPrompt = true;
    },
    onUp: () => {
      ffd20.skipConfirmPrompt = false;
    },
  });

  game.keybindings.register("ffd20", "forceShowItem", {
    name: "FFD20.KEYBINDINGS.ForceShowItem.Name",
    hint: game.i18n.localize("FFD20.KEYBINDINGS.ForceShowItem.Hint"),
    uneditable: CTRL_KEYS,
    onDown: () => {
      ffd20.forceShowItem = true;
    },
    onUp: () => {
      ffd20.forceShowItem = false;
    },
  });

  game.keybindings.register("ffd20", "hideTokenTooltip", {
    name: "FFD20.KEYBINDINGS.HideTokenTooltip.Name",
    hint: game.i18n.localize("FFD20.KEYBINDINGS.HideTokenTooltip.Hint"),
    uneditable: CTRL_KEYS,
    onDown: () => ffd20.documents.controls._hideTokenTooltip(true),
    onUp: () => ffd20.documents.controls._hideTokenTooltip(false),
  });

  game.keybindings.register("ffd20", "hideTokenTooltipGMInfo", {
    name: "FFD20.KEYBINDINGS.HideTokenTooltipGMInfo.Name",
    uneditable: SHIFT_KEYS,
    restricted: true,
    onDown: () => ffd20.documents.controls._hideTokenTooltipGMInfo(true),
    onUp: () => ffd20.documents.controls._hideTokenTooltipGMInfo(false),
  });
};

/**
 * Toggle the display of GM/Player info in the token tooltip
 *
 * @param {boolean} keyDown - Whether the key is pressed down
 * @returns {Promise<void>|void} A Promise that is resolved when the tooltip render handling is done
 */
export const _hideTokenTooltipGMInfo = (keyDown) => {
  if (!ffd20.tooltip) return;
  ffd20.tooltip.forceHideGMInfo = keyDown;
  return ffd20.tooltip?.refresh();
};

/**
 * Toggle the display of the token tooltip
 *
 * @param {boolean} keyDown - Whether the key is pressed down
 * @returns {Promise<void>|void} A Promise that is resolved when the tooltip render handling is done
 */
export const _hideTokenTooltip = (keyDown) => {
  if (!ffd20.tooltip) return;
  if (game.settings.get("ffd20", "tooltipConfig")?.hideWithoutKey === true) ffd20.tooltip.forceHide = !keyDown;
  else ffd20.tooltip.forceHide = keyDown;
  return ffd20.tooltip?.refresh();
};
