import { ItemFFD20 } from "./item-ffd20.mjs";

export class ItemAttackFFD20 extends ItemFFD20 {
  getConditionalTargets() {
    const result = super.getConditionalTargets();

    result["size"] = game.i18n.localize(CONFIG.FFD20.conditionalTargets.size._label);

    return result;
  }

  prepareData() {
    super.prepareData();

    const weaponGroups = this.system.weaponGroups || { value: [], custom: "" };

    weaponGroups.selected = weaponGroups.value.reduce((obj, t) => {
      obj[t] = CONFIG.FFD20.weaponGroups[t];
      return obj;
    }, {});

    // Add custom entry
    if (weaponGroups.custom) {
      weaponGroups.custom
        .split(CONFIG.FFD20.re.traitSeparator)
        .forEach((c, i) => (weaponGroups.selected[`custom${i + 1}`] = c.trim()));
    }

    weaponGroups.cssClass = foundry.utils.isEmpty(weaponGroups.selected) ? "inactive" : "";
  }

  get subType() {
    return this.system.attackType;
  }
}
