import { ActorSheetFFD20NPC } from "./npc-sheet.mjs";
import { splitCurrency } from "../../utils/lib.mjs";

export class ActorSheetFFD20NPCLoot extends ActorSheetFFD20NPC {
  /**
   * Define default rendering options for the NPC sheet
   *
   * @returns {object}
   */
  static get defaultOptions() {
    return mergeObject(super.defaultOptions, {
      classes: ["ffd20", "sheet", "actor", "npc", "loot"],
      tabs: [{ navSelector: "nav.tabs", contentSelector: "section.primary-body", initial: "inventory" }],
      width: 620,
      height: 420,
    });
  }

  get template() {
    return "systems/ffd20/templates/actors/npc-sheet-loot.hbs";
  }

  get currentPrimaryTab() {
    return "inventory";
  }

  async getData() {
    const data = await super.getData();

    data.isLootSheet = true;
    data.sellMultiplier = this.actor.getFlag("ffd20", "sellMultiplier");

    // Get total value
    const cgilValue =
      this.calculateTotalItemValue({ inLowestDenomination: true }) +
      this.actor.mergeCurrency({ inLowestDenomination: true });
    const cgilSellValue =
      this.calculateSellItemValue({ inLowestDenomination: true }) +
      this.actor.mergeCurrency({ inLowestDenomination: true });

    data.totalValue = splitCurrency(cgilValue);
    data.sellValue = splitCurrency(cgilSellValue);

    // Set labels
    if (!data.labels) data.labels = {};
    data.labels.totalValue = game.i18n
      .localize("FFD20.ItemContainerTotalValue")
      .format(data.totalValue.gil, data.totalValue.sgil, data.totalValue.cgil);
    data.labels.sellValue = game.i18n
      .localize("FFD20.ItemContainerSellValue")
      .format(data.sellValue.gil, data.sellValue.sgil, data.sellValue.cgil);

    // Alter inventory columns
    for (const inv of Object.values(data.inventory)) {
      inv.hasActions = false;
      inv.canEquip = false;
      inv.showValue = true;
    }

    return data;
  }
}
