/**
 * Polymorphic base class.
 * Should be fairly empty, only containing functionality that all items should have regardless of type.
 */
export class ItemBaseFFD20 extends Item {
  /**
   * Polymorphic constructor.
   *
   * @param {object} data ActorData
   * @param {object} context Context data
   */
  // eslint-disable-next-line constructor-super
  constructor(data, context = {}) {
    if (context.ffd20?.subtyped) {
      super(data, context);
    } else if (data.type) {
      const subtyped = { ffd20: { subtyped: true } };
      const cls = CONFIG.Item.documentClasses[data?.type ?? data.type] ?? CONFIG.Item.documentClasses.default;
      if (!cls) console.warn(data?.type, data.type);
      return new cls(data, { ...subtyped, ...context });
    }
  }

  /**
   * @returns {string} Item subtype.
   */
  get subType() {
    return null;
  }
}
