import { BaseRegistry, BaseRegistryObject } from "./base-registry.mjs";

export class ScriptCall extends BaseRegistryObject {
  /** @inheritdoc */
  static typeName = "Script Call";

  /** @inheritdoc */
  static get _baseData() {
    return mergeObject(super._baseData, {
      itemTypes: [],
      name: null,
      info: null,
    });
  }
}

export class ScriptCalls extends BaseRegistry {
  /** @inheritdoc */
  static contentClass = ScriptCall;

  /** @inheritdoc */
  static _defaultData = [
    // Use
    {
      _id: "use",
      itemTypes: ["attack", "feat", "equipment", "consumable", "spell", "weapon"],
      name: "FFD20.ScriptCalls.Use.Name",
      info: "FFD20.ScriptCalls.Use.Info",
    },
    // Equip
    {
      _id: "equip",
      itemTypes: ["weapon", "equipment", "loot"],
      name: "FFD20.ScriptCalls.Equip.Name",
      info: "FFD20.ScriptCalls.Equip.Info",
    },
    // Toggle
    {
      _id: "toggle",
      itemTypes: ["buff", "feat"],
      name: "FFD20.ScriptCalls.Toggle.Name",
      info: "FFD20.ScriptCalls.Toggle.Info",
    },
    // Change Quantity
    {
      _id: "changeQuantity",
      itemTypes: ["loot", "equipment", "weapon", "consumable", "container"],
      name: "FFD20.ScriptCalls.ChangeQuantity.Name",
      info: "FFD20.ScriptCalls.ChangeQuantity.Info",
    },
    // Change Level
    {
      _id: "changeLevel",
      itemTypes: ["buff", "class"],
      name: "FFD20.ScriptCalls.ChangeLevel.Name",
      info: "FFD20.ScriptCalls.ChangeLevel.Info",
    },
  ].map((d) => ({ ...d, module: "ffd20" }));
}

export const scriptCalls = new ScriptCalls();
