/**
 * Returns whether the user's settings and key presses signal that dialogs should be skipped.
 *
 * @returns {boolean}
 */
export const getSkipActionPrompt = function () {
  return (
    (game.settings.get("ffd20", "skipActionDialogs") && !ffd20.skipConfirmPrompt) ||
    (!game.settings.get("ffd20", "skipActionDialogs") && ffd20.skipConfirmPrompt)
  );
};
