import { FFD20 } from "../../../config.mjs";
import { RollFFD20 } from "../../../dice/roll.mjs";
import { fractionalToString } from "@utils";
import { callOldNamespaceHookAll } from "@utils/hooks.mjs";

/**
 * @this {import("@actor/actor-ffd20.mjs").ActorFFD20}
 */
export function applyChanges() {
  this.changeOverrides = {};
  const changes = Array.from(this.changes);

  const priority = getSortChangePriority.call(this);
  const _sortChanges = function (a, b) {
    const typeA = priority.types.indexOf(a.subTarget);
    const typeB = priority.types.indexOf(b.subTarget);
    const modA = priority.modifiers.indexOf(a.modifier);
    const modB = priority.modifiers.indexOf(b.modifier);
    let prioA = typeof a.priority === "string" ? parseInt(a.priority) : a.priority;
    let prioB = typeof b.priority === "string" ? parseInt(b.priority) : b.priority;
    prioA = (prioA || 0) + 1000;
    prioB = (prioB || 0) + 1000;

    return prioB - prioA || typeA - typeB || modA - modB;
  };

  // Organize changes by priority
  changes.sort((a, b) => _sortChanges.call(this, a, b));

  // Parse change flags
  for (const i of this.changeItems) {
    for (const [k, v] of Object.entries(i.system.changeFlags)) {
      if (v === true) {
        this.changeFlags[k] = true;

        if (k === "loseDexToAC") {
          for (const k2 of ["normal", "touch"]) {
            getSourceInfo(this.sourceInfo, `system.attributes.ac.${k2}.total`).negative.push({
              value: game.i18n.localize("FFD20.ChangeFlagLoseDexToAC"),
              name: i.name,
              type: i.type,
            });
          }
          getSourceInfo(this.sourceInfo, "system.attributes.cmd.total").negative.push({
            value: game.i18n.localize("FFD20.ChangeFlagLoseDexToAC"),
            name: i.name,
            type: i.type,
          });
        }
      }
    }
  }
  this.refreshDerivedData();

  // Determine continuous changes
  const continuousChanges = changes.filter((o) => o.continuous === true);

  // Apply all changes
  for (let a = 0; a < changes.length; a++) {
    const change = changes[a];
    let flats = getChangeFlat.call(this, change.subTarget, change.modifier);
    if (!(flats instanceof Array)) flats = [flats];
    for (const f of flats) {
      if (!this.changeOverrides[f]) this.changeOverrides[f] = createOverride();
    }

    change.applyChange(this, flats, { applySourceInfo: false });

    // Apply continuous changes
    for (const cc of continuousChanges) {
      if (cc === change) continue;

      let flats = getChangeFlat.call(this, cc.subTarget, cc.modifier);
      if (!(flats instanceof Array)) flats = [flats];
      for (const f of flats) {
        if (!this.changeOverrides[f]) this.changeOverrides[f] = createOverride();
      }

      cc.applyChange(this, flats, { applySourceInfo: false });
    }

    this.refreshDerivedData();
  }

  // Apply source info for changes
  for (const change of changes) {
    change.applySourceInfo(this);
  }

  resetSkills.call(this);
}

const createOverride = function () {
  const result = {
    add: {},
    set: {},
  };

  for (const k of Object.keys(CONFIG.FFD20.bonusModifiers)) {
    result.add[k] = null;
    result.set[k] = null;
  }

  return result;
};

const getSortChangePriority = function () {
  /** @type {[string, {sort: number}][]}*/
  const skillTargets = this._skillTargets.map((target, index) => [target, { sort: 76000 + index * 10 }]);
  const buffTargets = Object.entries(FFD20.buffTargets);
  const types = [...skillTargets, ...buffTargets]
    .sort(([, { sort: aSort }], [, { sort: bSort }]) => aSort - bSort)
    .map(([target]) => target);

  return {
    types: types,
    modifiers: [
      "untyped",
      "untypedPerm",
      "base",
      "enh",
      "dodge",
      "inherent",
      "deflection",
      "morale",
      "luck",
      "sacred",
      "insight",
      "resist",
      "profane",
      "trait",
      "racial",
      "size",
      "competence",
      "circumstance",
      "alchemical",
      "penalty",
    ],
  };
};

export const getChangeFlat = function (changeTarget, changeType, curData = null) {
  if (changeTarget == null) return null;

  curData = curData ?? this.system;
  /** @type {string[]} */
  const result = [];

  switch (changeTarget) {
    case "mhp":
      result.push("system.attributes.hp.max");
      break;
    case "wounds":
      result.push("system.attributes.wounds.max");
      break;
    case "vigor":
      result.push("system.attributes.vigor.max");
      break;
    case "mmp":
      result.push("system.attributes.mp.max");
      break;
    case "limitbreak":
      result.push("system.attributes.limitbreak.max");
      break;
    case "str":
    case "dex":
    case "con":
    case "int":
    case "wis":
    case "cha":
      if (changeType === "penalty") {
        result.push(`system.abilities.${changeTarget}.penalty`);
        break;
      }
      if (["base", "untypedPerm"].includes(changeType)) {
        result.push(`system.abilities.${changeTarget}.total`, `system.abilities.${changeTarget}.base`);
        break;
      }
      result.push(`system.abilities.${changeTarget}.total`);
      break;
    case "strMod":
    case "dexMod":
    case "conMod":
    case "intMod":
    case "wisMod":
    case "chaMod":
      result.push(`system.abilities.${changeTarget.slice(0, 3)}.mod`);
      break;
    case "carryStr":
      result.push("system.details.carryCapacity.bonus.total");
      break;
    case "carryMult":
      result.push("system.details.carryCapacity.multiplier.total");
      break;
    case "ac":
      switch (changeType) {
        case "dodge":
          result.push(
            "system.attributes.ac.normal.total",
            "system.attributes.ac.touch.total",
            "system.attributes.cmd.total"
          );
          break;
        case "deflection":
          result.push(
            "system.attributes.ac.normal.total",
            "system.attributes.ac.touch.total",
            "system.attributes.ac.flatFooted.total",
            "system.attributes.cmd.total",
            "system.attributes.cmd.flatFootedTotal"
          );
          break;
        case "circumstance":
        case "insight":
        case "luck":
        case "morale":
        case "profane":
        case "sacred":
          result.push(
            "system.attributes.ac.normal.total",
            "system.attributes.ac.touch.total",
            "system.attributes.ac.flatFooted.total",
            "system.attributes.cmd.total",
            "system.attributes.cmd.flatFootedTotal"
          );
          break;
        default:
          result.push(
            "system.attributes.ac.normal.total",
            "system.attributes.ac.touch.total",
            "system.attributes.ac.flatFooted.total"
          );
          break;
      }
      break;
    case "aac": {
      const targets = ["system.ac.normal.total"];
      switch (changeType) {
        case "base":
          targets.push("system.ac.normal.base");
          break;
        case "enh":
          targets.push("system.ac.normal.enh");
          break;
        default:
          targets.push("system.ac.normal.misc");
          break;
      }
      result.push(...targets);
      break;
    }
    case "sac": {
      const targets = ["system.ac.shield.total"];
      switch (changeType) {
        case "base":
          targets.push("system.ac.shield.base");
          break;
        case "enh":
          targets.push("system.ac.shield.enh");
          break;
        default:
          targets.push("system.ac.shield.misc");
          break;
      }
      result.push(...targets);
      break;
    }
    case "nac": {
      const targets = ["system.ac.natural.total"];
      switch (changeType) {
        case "base":
          targets.push("system.ac.natural.base");
          break;
        case "enh":
          targets.push("system.ac.natural.enh");
          break;
        default:
          targets.push("system.ac.natural.misc");
          break;
      }
      result.push(...targets);
      break;
    }
    case "tac":
      result.push("system.attributes.ac.touch.total");
      break;
    case "ffac":
      result.push("system.attributes.ac.flatFooted.total");
      break;
    case "ffcmd":
      result.push("system.attributes.cmd.flatFootedTotal");
      break;
    case "bab":
      result.push("system.attributes.bab.total");
      break;
    case "~attackCore":
      result.push("system.attributes.attack.shared");
      break;
    case "attack":
      result.push("system.attributes.attack.general");
      break;
    case "mattack":
      result.push("system.attributes.attack.melee");
      break;
    case "rattack":
      result.push("system.attributes.attack.ranged");
      break;
    case "critConfirm":
      result.push("system.attributes.attack.critConfirm");
      break;
    case "allSavingThrows":
      result.push(
        "system.attributes.savingThrows.fort.total",
        "system.attributes.savingThrows.ref.total",
        "system.attributes.savingThrows.will.total"
      );
      break;
    case "fort":
      result.push("system.attributes.savingThrows.fort.total");
      break;
    case "ref":
      result.push("system.attributes.savingThrows.ref.total");
      break;
    case "will":
      result.push("system.attributes.savingThrows.will.total");
      break;
    case "skills":
      for (const [a, skl] of Object.entries(curData.skills)) {
        if (skl == null) continue;
        result.push(`system.skills.${a}.changeBonus`);

        if (skl.subSkills != null) {
          for (const b of Object.keys(skl.subSkills)) {
            result.push(`system.skills.${a}.subSkills.${b}.changeBonus`);
          }
        }
      }
      break;
    case "~skillMods":
      for (const [a, skl] of Object.entries(curData.skills)) {
        if (skl == null) continue;
        result.push(`system.skills.${a}.mod`);

        if (skl.subSkills != null) {
          for (const b of Object.keys(skl.subSkills)) {
            result.push(`system.skills.${a}.subSkills.${b}.mod`);
          }
        }
      }
      break;
    case "strSkills":
      for (const [a, skl] of Object.entries(curData.skills)) {
        if (skl == null) continue;
        if (skl.ability === "str") result.push(`system.skills.${a}.changeBonus`);

        if (skl.subSkills != null) {
          for (const [b, subSkl] of Object.entries(skl.subSkills)) {
            if (subSkl != null && subSkl.ability === "str")
              result.push(`system.skills.${a}.subSkills.${b}.changeBonus`);
          }
        }
      }
      break;
    case "dexSkills":
      for (const [a, skl] of Object.entries(curData.skills)) {
        if (skl == null) continue;
        if (skl.ability === "dex") result.push(`system.skills.${a}.changeBonus`);

        if (skl.subSkills != null) {
          for (const [b, subSkl] of Object.entries(skl.subSkills)) {
            if (subSkl != null && subSkl.ability === "dex")
              result.push(`system.skills.${a}.subSkills.${b}.changeBonus`);
          }
        }
      }
      break;
    case "conSkills":
      for (const [a, skl] of Object.entries(curData.skills)) {
        if (skl == null) continue;
        if (skl.ability === "con") result.push(`system.skills.${a}.changeBonus`);

        if (skl.subSkills != null) {
          for (const [b, subSkl] of Object.entries(skl.subSkills)) {
            if (subSkl != null && subSkl.ability === "con")
              result.push(`system.skills.${a}.subSkills.${b}.changeBonus`);
          }
        }
      }
      break;
    case "intSkills":
      for (const [a, skl] of Object.entries(curData.skills)) {
        if (skl == null) continue;
        if (skl.ability === "int") result.push(`system.skills.${a}.changeBonus`);

        if (skl.subSkills != null) {
          for (const [b, subSkl] of Object.entries(skl.subSkills)) {
            if (subSkl != null && subSkl.ability === "int")
              result.push(`system.skills.${a}.subSkills.${b}.changeBonus`);
          }
        }
      }
      break;
    case "wisSkills":
      for (const [a, skl] of Object.entries(curData.skills)) {
        if (skl == null) continue;
        if (skl.ability === "wis") result.push(`system.skills.${a}.changeBonus`);

        if (skl.subSkills != null) {
          for (const [b, subSkl] of Object.entries(skl.subSkills)) {
            if (subSkl != null && subSkl.ability === "wis")
              result.push(`system.skills.${a}.subSkills.${b}.changeBonus`);
          }
        }
      }
      break;
    case "chaSkills":
      for (const [a, skl] of Object.entries(curData.skills)) {
        if (skl == null) continue;
        if (skl.ability === "cha") result.push(`system.skills.${a}.changeBonus`);

        if (skl.subSkills != null) {
          for (const [b, subSkl] of Object.entries(skl.subSkills)) {
            if (subSkl != null && subSkl.ability === "cha")
              result.push(`system.skills.${a}.subSkills.${b}.changeBonus`);
          }
        }
      }
      break;
    case "allChecks":
      result.push(
        "system.abilities.str.checkMod",
        "system.abilities.dex.checkMod",
        "system.abilities.con.checkMod",
        "system.abilities.int.checkMod",
        "system.abilities.wis.checkMod",
        "system.abilities.cha.checkMod",
        ...(this.system.attributes.init.ability ? ["system.attributes.init.total"] : [])
      );
      break;
    case "strChecks":
      result.push(
        "system.abilities.str.checkMod",
        ...(this.system.attributes.init.ability === "str" ? ["system.attributes.init.total"] : [])
      );
      break;
    case "dexChecks":
      result.push(
        "system.abilities.dex.checkMod",
        ...(this.system.attributes.init.ability === "dex" ? ["system.attributes.init.total"] : [])
      );
      break;
    case "conChecks":
      result.push(
        "system.abilities.con.checkMod",
        ...(this.system.attributes.init.ability === "con" ? ["system.attributes.init.total"] : [])
      );
      break;
    case "intChecks":
      result.push(
        "system.abilities.int.checkMod",
        ...(this.system.attributes.init.ability === "int" ? ["system.attributes.init.total"] : [])
      );
      break;
    case "wisChecks":
      result.push(
        "system.abilities.wis.checkMod",
        ...(this.system.attributes.init.ability === "wis" ? ["system.attributes.init.total"] : [])
      );
      break;
    case "chaChecks":
      result.push(
        "system.abilities.cha.checkMod",
        ...(this.system.attributes.init.ability === "cha" ? ["system.attributes.init.total"] : [])
      );
      break;
    case "allSpeeds":
      for (const speedKey of Object.keys(curData.attributes.speed)) {
        const base = curData.attributes.speed[speedKey]?.base;
        if (base !== undefined) result.push(`system.attributes.speed.${speedKey}.total`);
      }
      break;
    case "landSpeed":
      if (changeType === "base") return ["system.attributes.speed.land.total"];
      result.push("system.attributes.speed.land.add", "system.attributes.speed.land.total");
      break;
    case "climbSpeed":
      if (changeType === "base") {
        result.push("system.attributes.speed.climb.total");
        break;
      }
      result.push("system.attributes.speed.climb.add", "system.attributes.speed.climb.total");
      break;
    case "swimSpeed":
      if (changeType === "base") {
        result.push("system.attributes.speed.swim.total");
        break;
      }
      result.push("system.attributes.speed.swim.add", "system.attributes.speed.swim.total");
      break;
    case "burrowSpeed":
      if (changeType === "base") {
        result.push("system.attributes.speed.burrow.total");
        break;
      }
      result.push("system.attributes.speed.burrow.add", "system.attributes.speed.burrow.total");
      break;
    case "flySpeed":
      if (changeType === "base") {
        result.push("system.attributes.speed.fly.total");
        break;
      }
      result.push("system.attributes.speed.fly.add", "system.attributes.speed.fly.total");
      break;
    case "cmb":
      result.push("system.attributes.cmb.bonus");
      break;
    case "cmd":
      if (changeType === "dodge") {
        result.push("system.attributes.cmd.total");
        break;
      }
      result.push("system.attributes.cmd.total", "system.attributes.cmd.flatFootedTotal");
      break;
    case "init":
      result.push("system.attributes.init.total");
      break;
    case "acpA":
      result.push("system.attributes.acp.armorBonus");
      break;
    case "acpS":
      result.push("system.attributes.acp.shieldBonus");
      break;
    case "mDexA":
      result.push("system.attributes.mDex.armorBonus");
      break;
    case "mDexS":
      result.push("system.attributes.mDex.shieldBonus");
      break;
    case "spellResist":
      result.push("system.attributes.sr.total");
      break;
    case "damage":
      result.push("system.attributes.damage.general");
      break;
    case "wdamage":
      result.push("system.attributes.damage.weapon");
      break;
    case "sdamage":
      result.push("system.attributes.damage.spell");
      break;
    case "bonusFeats":
      result.push("system.details.feats.bonus");
      break;
    case "bonusSkillRanks":
      result.push("system.details.skills.bonus");
      break;
    case "concentration":
      result.push(
        "system.attributes.spells.spellbooks.primary.concentration.total",
        "system.attributes.spells.spellbooks.secondary.concentration.total",
        "system.attributes.spells.spellbooks.tertiary.concentration.total",
        "system.attributes.spells.spellbooks.Quaternary.concentration.total",
        "system.attributes.spells.spellbooks.spelllike.concentration.total"
      );
      break;
    case "cl":
      result.push(
        "system.attributes.spells.spellbooks.primary.cl.total",
        "system.attributes.spells.spellbooks.secondary.cl.total",
        "system.attributes.spells.spellbooks.tertiary.cl.total",
        "system.attributes.spells.spellbooks.Quaternary.cl.total",
        "system.attributes.spells.spellbooks.spelllike.cl.total"
      );
      break;
  }

  if (changeTarget.match(/^skill\.([a-zA-Z0-9]+)$/)) {
    const sklKey = RegExp.$1;
    if (curData.skills[sklKey] != null) {
      result.push(`system.skills.${sklKey}.changeBonus`);
    }
  } else if (changeTarget.match(/^skill\.([a-zA-Z0-9]+)\.subSkills\.([a-zA-Z0-9_]+)$/)) {
    const sklKey = RegExp.$1;
    const subSklKey = RegExp.$2;
    if (curData.skills[sklKey]?.subSkills?.[subSklKey] != null) {
      result.push(`system.skills.${sklKey}.subSkills.${subSklKey}.changeBonus`);
    }
  }

  // Call hooks to enable modules to add or adjust the result array
  callOldNamespaceHookAll("ffd20.getChangeFlat", "ffd20GetChangeFlat", changeTarget, changeType, { keys: result });
  Hooks.callAll("ffd20GetChangeFlat", changeTarget, changeType, result, curData);

  // Return results directly when deprecation is removed
  return result.map((key) => {
    const fixedKey = key.replace("data.", "system.");
    if (key.startsWith("data.")) {
      foundry.utils.logCompatibilityWarning(
        `Change targets pointing towards "data." (${key}) are deprecated. Use "system." (${fixedKey}) instead.`,
        { since: "FFD20 0.82.0", until: "FFD20 0.83.0" }
      );
      return fixedKey;
    }
    return key;
  });
};

const getBabTotal = function (d) {
  return d.attributes.bab.total;
};

const getNegativeEnergyDrain = function (d) {
  return -d.attributes.energyDrain;
};

const getAbilityMod = function (ability) {
  return function (d) {
    return d.abilities[ability]?.mod ?? 0;
  };
};

export const addDefaultChanges = function (changes) {
  const actorData = this.system;
  // Call hook
  const tempChanges = [];
  callOldNamespaceHookAll("ffd20.addDefaultChanges", "ffd20AddDefaultChanges", this, tempChanges);
  Hooks.callAll("ffd20AddDefaultChanges", this, tempChanges);
  changes.push(...tempChanges.filter((c) => c instanceof ffd20.components.ItemChange));

  // Class hit points
  const classes = this.items
    .filter((o) => o.type === "class" && !["racial"].includes(o.classType))
    .sort((a, b) => {
      return a.sort - b.sort;
    });
  const racialHD = this.items
    .filter((o) => o.type === "class" && o.classType === "racial")
    .sort((a, b) => {
      return a.sort - b.sort;
    });

  const healthConfig = game.settings.get("ffd20", "healthConfig");
  const cls_options = this.type === "character" ? healthConfig.hitdice.PC : healthConfig.hitdice.NPC;
  const race_options = healthConfig.hitdice.Racial;
  const round = { up: Math.ceil, nearest: Math.round, down: Math.floor }[healthConfig.rounding];
  const continuous = { discrete: false, continuous: true }[healthConfig.continuity];

  const push_health = (value, source) => {
    changes.push(
      new ffd20.components.ItemChange({
        formula: value,
        target: "misc",
        subTarget: "mhp",
        modifier: "untypedPerm",
        flavor: source.name,
      })
    );
    changes.push(
      new ffd20.components.ItemChange({
        formula: value,
        target: "misc",
        subTarget: "vigor",
        modifier: "untypedPerm",
        flavor: source.name,
      })
    );
  };
  const manual_health = (health_source) => {
    let health =
      health_source.system.hp + (health_source.system.classType === "base") * health_source.system.fc.hp.value;

    if (!continuous) health = round(health);
    push_health(health, health_source);
  };
  const auto_health = (health_source, options, maximized = 0) => {
    if (health_source.system.hd === 0) return;

    let die_health = 1 + (health_source.system.hd - 1) * options.rate;
    if (!continuous) die_health = round(die_health);

    const maxed_health = Math.min(health_source.system.level, maximized) * health_source.system.hd;
    const level_health = Math.max(0, health_source.system.level - maximized) * die_health;
    const favor_health = (health_source.system.classType === "base") * health_source.system.fc.hp.value;
    const health = maxed_health + level_health + favor_health;

    push_health(health, health_source);
  };
  const compute_health = (health_sources, options) => {
    // Compute and push health, tracking the remaining maximized levels.
    if (options.auto) {
      let maximized = options.maximized;
      for (const hd of health_sources) {
        auto_health(hd, options, maximized);
        maximized = Math.max(0, maximized - hd.system.level);
      }
    } else health_sources.forEach((race) => manual_health(race));
  };

  compute_health(racialHD, race_options);
  compute_health(classes, cls_options);

  // Class mana points, call class and race info from above

  const push_mana = (value, source) => {
    changes.push(
      new ffd20.components.ItemChange({
        formula: value.toString(),
        target: "misc",
        subTarget: "mmp",
        modifier: "untypedPerm",
        flavor: source.name,
      })
    );
  };

  const manual_mana = (mana_source) => {
    const mana = mana_source.system.mp === null ? 0 : mana_source.system.mp;
    getSourceInfo(this.sourceInfo, "system.attributes.mp.max").positive.push({
      value: mana_source.system.mp,
      name: game.i18n.format("FFD20.SourceInfoSkillRank_ClassBase", { className: mana_source.name }),
    });
    push_mana(mana, mana_source);
  };

  const auto_mana = (mana_source) => {
    if (mana_source.system.classBaseMPTypes === "noncaster") return;
    if (mana_source.system.level === 0) return;
    // figure out how much mp comes from the class
    let mult = 1;
    if (mana_source.system.classBaseMPauto === "half") mult = 0.5;
    const manaChart = CONFIG.FFD20.classMPlevels[mana_source.system.classBaseMPTypes];
    const level_mana = Math.floor(manaChart[mana_source.system.level - 1] * mult);
    push_mana(level_mana, mana_source);
  };

  /* Compute and push mana, tracking the remaining maximized levels.
   * for each class, check if it is calulating mp or defaulting to manually entered value
   * step 1 - gather all classes
   * step 2 - check for each if it is auto or manual
   * step 3 - pass to correct function
   * step 4 - if manual just add mp to pool
   * step 5 - for auto follow substeps
   * the following is for each class passed to it
   * step 5a - condence relevant variables
   *   MP type: which mp chart does it look at - mana_source.system. classBaseMPTypes
   *   Max spell lvl: which spell prog does it look at - mana_source.system.classBaseMPTypes
   *   level: level of the class - mana_source.system.level
   *   casting stat: what stat(s) are used for it - mana_source.system.classCastingStat
   * pass result
   * else
   * pass manual mp value
   * total results
   */

  // TODO adjust for class autos
  // check each for false or null, do first manual option, if true move to auto

  const compute_mana = (mana_sources) => {
    mana_sources.forEach((mpsource) => {
      if (mpsource.system.classBaseMPauto == "no" || mpsource.system.classBaseMPauto == null) {
        manual_mana(mpsource);
      } else {
        auto_mana(mpsource);
      }
    });
  };

  compute_mana(racialHD);
  compute_mana(classes);

  // Add class data to saving throws
  const allClasses = [...classes, ...racialHD];
  const useFractional = game.settings.get("ffd20", "useFractionalBaseBonuses") === true;
  for (const a of Object.keys(actorData.attributes.savingThrows)) {
    let hasGoodSave = false;
    actorData.attributes.savingThrows[a].total = actorData.attributes.savingThrows[a]?.base ?? 0;

    const total = allClasses.reduce((cur, cls) => {
      const base = cls.system.savingThrows[a].base;

      if (!useFractional) {
        // Add per class change
        changes.push(
          new ffd20.components.ItemChange({
            formula: base,
            target: "savingThrows",
            subTarget: a,
            modifier: "untypedPerm",
            flavor: cls.name,
          })
        );
      } else {
        if (cls.system.savingThrows[a].good === true) hasGoodSave = true;
      }
      return cur + base;
    }, 0);

    if (useFractional) {
      // Add shared change with fractional
      changes.push(
        new ffd20.components.ItemChange({
          formula: Math.floor(total),
          target: "savingThrows",
          subTarget: a,
          modifier: "untypedPerm",
          flavor: game.i18n.localize("FFD20.Base"),
        })
      );
    }

    // Fractional bonus +2 when one class has good save
    if (useFractional && hasGoodSave) {
      const goodSaveFormula = CONFIG.FFD20.classFractionalSavingThrowFormulas.goodSaveBonus;
      const total = RollFFD20.safeRoll(goodSaveFormula).total;
      changes.push(
        new ffd20.components.ItemChange({
          formula: total,
          target: "savingThrows",
          subTarget: a,
          modifier: "untypedPerm",
          flavor: game.i18n.localize("FFD20.SavingThrowGoodFractionalBonus"),
        })
      );
    }
  }

  // Add Constitution to HP
  const hpAbility = actorData.attributes.hpAbility;
  if (hpAbility) {
    changes.push(
      new ffd20.components.ItemChange({
        formula: (d) => d.abilities[hpAbility].mod * d.attributes.hd.total,
        operator: "function",
        target: "misc",
        subTarget: "mhp",
        modifier: "base",
        flavor: CONFIG.FFD20.abilities[hpAbility],
      })
    );

    if (!getProperty(this, "system.attributes.wounds.base")) {
      changes.push(
        new ffd20.components.ItemChange({
          formula: (d) => d.abilities[hpAbility].total * 2 + d.abilities[hpAbility].drain,
          operator: "function",
          target: "misc",
          subTarget: "wounds",
          modifier: "base",
          flavor: CONFIG.FFD20.abilities[hpAbility],
        })
      );
    }
  }

  // add bonus mp and does it after all stat mods
  classes.forEach((mp_source) => {
    let mpAbility = mp_source.system.classCastingStat;
    if (mpAbility == null) mpAbility = "noncaster";
    if (mpAbility === "noncaster") return;
    if (mp_source.system.level === 0) return;
    const spellMath = CONFIG.FFD20.ClassSpellLvlProgression[mp_source.system.classBaseMPTypes];
    const currentSpellLvl = Math.floor(RollFFD20.safeRoll(spellMath, { level: mp_source.system.level }).total);
    const mpProg = CONFIG.FFD20.classMPStatsBonus[currentSpellLvl];
    if (mpAbility !== "") {
      const arrayStr = JSON.stringify(mpProg);
      if (mpAbility === "intAndWis") {
        changes.push(
          new ffd20.components.ItemChange({
            formula: `${arrayStr}[[@abilities.int.mod]] + ${arrayStr}[[@abilities.wis.mod]]`,
            target: "misc",
            subTarget: "mmp",
            modifier: "base",
          })
        );
      } else {
        changes.push(
          new ffd20.components.ItemChange({
            formula: `${arrayStr}[[@abilities.${mpAbility}.mod]]`,
            target: "misc",
            subTarget: "mmp",
            modifier: "base",
          })
        );
      }
    }
  });

  // Add movement speed(s)
  for (const [k, s] of Object.entries(actorData.attributes.speed)) {
    let base = s.base;
    if (!base) base = 0;
    changes.push(
      new ffd20.components.ItemChange({
        formula: base,
        target: "speed",
        subTarget: `${k}Speed`,
        modifier: "base",
        operator: "set",
        priority: 1001,
        flavor: game.i18n.localize("FFD20.Base"),
      })
    );
  }

  // Add base attack modifiers shared by all attacks
  {
    // BAB to attack
    changes.push(
      new ffd20.components.ItemChange({
        formula: getBabTotal,
        operator: "function",
        target: "attack",
        subTarget: "~attackCore",
        modifier: "untypedPerm",
        flavor: game.i18n.localize("FFD20.BAB"),
      })
    );
    // Energy drain to attack
    changes.push(
      new ffd20.components.ItemChange({
        formula: getNegativeEnergyDrain,
        operator: "function",
        target: "attack",
        subTarget: "~attackCore",
        modifier: "untypedPerm",
        flavor: game.i18n.localize("FFD20.CondTypeEnergyDrain"),
      })
    );
    // ACP to attack
    changes.push(
      new ffd20.components.ItemChange({
        formula: (d) => -d.attributes.acp.attackPenalty,
        operator: "function",
        target: "attack",
        subTarget: "~attackCore",
        modifier: "penalty",
        flavor: game.i18n.localize("FFD20.ArmorCheckPenalty"),
      })
    );
  }

  // Add variables to CMD
  {
    // BAB to CMD
    changes.push(
      new ffd20.components.ItemChange({
        formula: getBabTotal,
        operator: "function",
        target: "misc",
        subTarget: "cmd",
        modifier: "untypedPerm",
        flavor: game.i18n.localize("FFD20.BAB"),
      })
    );
    // Strength or substitute to CMD
    const strAbl = actorData.attributes.cmd.strAbility;
    if (strAbl in CONFIG.FFD20.abilities) {
      changes.push(
        new ffd20.components.ItemChange({
          formula: `@abilities.${strAbl}.mod`,
          target: "misc",
          subTarget: "cmd",
          modifier: "untypedPerm",
          flavor: CONFIG.FFD20.abilities[strAbl],
        })
      );
    }
    // Energy Drain to CMD
    changes.push(
      new ffd20.components.ItemChange({
        formula: getNegativeEnergyDrain,
        operator: "function",
        target: "misc",
        subTarget: "cmd",
        modifier: "untypedPerm",
        flavor: game.i18n.localize("FFD20.CondTypeEnergyDrain"),
      })
    );
  }

  // Add Dexterity Modifier to Initiative
  {
    const abl = actorData.attributes.init.ability;
    if (abl) {
      changes.push(
        new ffd20.components.ItemChange({
          formula: getAbilityMod(abl),
          operator: "function",
          target: "misc",
          subTarget: "init",
          modifier: "untypedPerm",
          priority: -100,
          flavor: CONFIG.FFD20.abilities[abl],
        })
      );
    }

    // Add ACP penalty
    if (["str", "dex"].includes(abl)) {
      changes.push(
        new ffd20.components.ItemChange({
          formula: (d) => -d.attributes.acp.attackPenalty,
          operator: "function",
          target: "misc",
          subTarget: "init",
          modifier: "penalty",
          priority: -100,
          flavor: game.i18n.localize("FFD20.ArmorCheckPenalty"),
        })
      );
    }
  }

  // Add Ability modifiers and Energy Drain to saving throws
  {
    // Ability Mod to Fortitude
    let abl = actorData.attributes.savingThrows.fort.ability;
    if (abl) {
      changes.push(
        new ffd20.components.ItemChange({
          formula: getAbilityMod(abl),
          operator: "function",
          target: "savingThrows",
          subTarget: "fort",
          modifier: "untypedPerm",
          flavor: CONFIG.FFD20.abilities[abl],
        })
      );
    }
    // Ability Mod to Reflex
    abl = actorData.attributes.savingThrows.ref.ability;
    if (abl) {
      changes.push(
        new ffd20.components.ItemChange({
          formula: getAbilityMod(abl),
          operator: "function",
          target: "savingThrows",
          subTarget: "ref",
          modifier: "untypedPerm",
          flavor: CONFIG.FFD20.abilities[abl],
        })
      );
    }
    // Ability Mod to Will
    abl = actorData.attributes.savingThrows.will.ability;
    if (abl) {
      changes.push(
        new ffd20.components.ItemChange({
          formula: getAbilityMod(abl),
          operator: "function",
          target: "savingThrows",
          subTarget: "will",
          modifier: "untypedPerm",
          flavor: CONFIG.FFD20.abilities[abl],
        })
      );
    }
    // Energy Drain
    changes.push(
      new ffd20.components.ItemChange({
        formula: getNegativeEnergyDrain,
        operator: "function",
        target: "savingThrows",
        subTarget: "allSavingThrows",
        modifier: "penalty",
        flavor: game.i18n.localize("FFD20.CondTypeEnergyDrain"),
      })
    );
  }
  // Spell Resistance
  {
    const sr = actorData.attributes.sr.formula || 0;
    changes.push(
      new ffd20.components.ItemChange({
        formula: sr,
        target: "misc",
        subTarget: "spellResist",
        modifier: "base",
        priority: 1000,
        flavor: game.i18n.localize("FFD20.Base"),
      })
    );
  }
  {
    // Carry capacity strength bonus
    const cStr = actorData.details.carryCapacity.bonus.user || 0;
    changes.push(
      new ffd20.components.ItemChange({
        formula: cStr,
        target: "misc",
        subTarget: "carryStr",
        modifier: "untyped",
        priority: 1000,
        flavor: game.i18n.localize("FFD20.Custom"),
      })
    );
    // Carry capacity multiplier
    const cMultBase = actorData.details.carryCapacity.multiplier.base ?? 1;
    changes.push(
      new ffd20.components.ItemChange({
        formula: cMultBase,
        target: "misc",
        subTarget: "carryMult",
        modifier: "base",
        priority: 1000,
        flavor: game.i18n.localize("FFD20.Base"),
      })
    );
    const cMult = actorData.details.carryCapacity.multiplier.user || 0;
    changes.push(
      new ffd20.components.ItemChange({
        formula: cMult,
        target: "misc",
        subTarget: "carryMult",
        modifier: "untyped",
        priority: 1000,
        flavor: game.i18n.localize("FFD20.Custom"),
      })
    );
  }
  // Natural armor
  {
    const ac = actorData.attributes.naturalAC || 0;
    changes.push(
      new ffd20.components.ItemChange({
        formula: ac,
        subTarget: "nac",
        modifier: "base",
        flavor: game.i18n.localize("FFD20.EquipTypeNatural"),
      })
    );
  }
  // Add armor bonuses from equipment
  this.items
    .filter((obj) => {
      return obj.type === "equipment" && obj.system.equipped;
    })
    .forEach((item) => {
      let armorTarget = "aac";
      if (item.system.equipmentType === "shield") armorTarget = "sac";
      // Push base armor
      if (item.system.armor.value || item.system.armor.enh) {
        let ac = item.system.armor.value + item.system.armor.enh;
        if (item.system.broken) ac = Math.floor(ac / 2);
        changes.push(
          new ffd20.components.ItemChange(
            {
              formula: item.system.armor.value,
              subTarget: armorTarget,
              modifier: "base",
            },
            item
          )
        );
        changes.push(
          new ffd20.components.ItemChange(
            {
              formula: item.system.armor.enh,
              subTarget: armorTarget,
              modifier: "enhancement",
            },
            item
          )
        );
      }
    });

  // Add fly bonuses or penalties based on maneuverability
  {
    const flyKey = actorData.attributes.speed.fly.maneuverability;
    let flyValue = 0;
    if (flyKey != null) flyValue = CONFIG.FFD20.flyManeuverabilityValues[flyKey];
    if (flyValue !== 0) {
      changes.push(
        new ffd20.components.ItemChange({
          formula: flyValue,
          target: "skill",
          subTarget: "skill.fly",
          modifier: "racial",
          flavor: game.i18n.localize("FFD20.FlyManeuverability"),
        })
      );
    }
  }
  // Add swim and climb skill bonuses based on having speeds for them
  {
    changes.push(
      new ffd20.components.ItemChange({
        formula: (d) => (d.attributes.speed.climb.total > 0 ? 8 : 0),
        operator: "function",
        target: "skill",
        subTarget: "skill.clm",
        modifier: "racial",
        priority: -1,
        flavor: game.i18n.localize("FFD20.SpeedClimb"),
      })
    );

    changes.push(
      new ffd20.components.ItemChange({
        formula: (d) => (d.attributes.speed.swim.total > 0 ? 8 : 0),
        operator: "function",
        target: "skill",
        subTarget: "skill.swm",
        modifier: "racial",
        priority: -1,
        flavor: game.i18n.localize("FFD20.SpeedSwim"),
      })
    );
  }

  // Add energy drain to skills
  {
    changes.push(
      new ffd20.components.ItemChange({
        formula: getNegativeEnergyDrain,
        operator: "function",
        target: "skills",
        subTarget: "skills",
        modifier: "untypedPerm",
        flavor: game.i18n.localize("FFD20.CondTypeEnergyDrain"),
      })
    );
  }

  // Add size bonuses to various attributes
  const sizeKey = actorData.traits.size;
  if (sizeKey !== "med") {
    // AC
    changes.push(
      new ffd20.components.ItemChange({
        formula: CONFIG.FFD20.sizeMods[sizeKey],
        target: "ac",
        subTarget: "ac",
        modifier: "size",
        flavor: game.i18n.localize("FFD20.BonusModifierSize"),
      })
    );
    // Stealth skill
    changes.push(
      new ffd20.components.ItemChange({
        formula: CONFIG.FFD20.sizeStealthMods[sizeKey],
        target: "skill",
        subTarget: "skill.ste",
        modifier: "size",
        flavor: game.i18n.localize("FFD20.BonusModifierSize"),
      })
    );
    // Fly skill
    changes.push(
      new ffd20.components.ItemChange({
        formula: CONFIG.FFD20.sizeFlyMods[sizeKey],
        target: "skill",
        subTarget: "skill.fly",
        modifier: "size",
        flavor: game.i18n.localize("FFD20.BonusModifierSize"),
      })
    );
    // CMD
    changes.push(
      new ffd20.components.ItemChange({
        formula: CONFIG.FFD20.sizeSpecialMods[sizeKey],
        target: "misc",
        subTarget: "cmd",
        modifier: "size",
        flavor: game.i18n.localize("FFD20.BonusModifierSize"),
      })
    );
  }

  // Add conditions
  for (const [con, v] of Object.entries(actorData.attributes.conditions || {})) {
    if (!v) continue;

    const mechanic = CONFIG.FFD20.conditionMechanics[con];
    if (!mechanic) continue;

    // Add changes
    for (const change of mechanic.changes ?? []) {
      // Alter change data
      const changeData = deepClone(change);
      changeData.flavor = CONFIG.FFD20.conditions[con];

      // Create change object
      const changeObj = new ffd20.components.ItemChange(changeData);
      changes.push(changeObj);
    }

    // Set flags
    for (const flag of mechanic.flags ?? []) {
      this.changeFlags[flag] = true;
    }
  }

  // Apply level drain to hit points
  if (!Number.isNaN(actorData.attributes.energyDrain) && actorData.attributes.energyDrain > 0) {
    changes.push(
      new ffd20.components.ItemChange({
        formula: (d) => -d.attributes.energyDrain * 5,
        operator: "function",
        subTarget: "mhp",
        modifier: "untyped",
        priority: -750,
        flavor: game.i18n.localize("FFD20.CondTypeEnergyDrain"),
      })
    );

    changes.push(
      new ffd20.components.ItemChange({
        formula: (d) => -d.attributes.energyDrain * 5,
        operator: "function",
        subTarget: "vigor",
        modifier: "untyped",
        priority: -750,
        flavor: game.i18n.localize("FFD20.CondTypeEnergyDrain"),
      })
    );
  }
};

const resetSkills = function () {
  const actorData = this.system;
  const skills = actorData.skills;

  for (const [sklKey, skl] of Object.entries(skills)) {
    if (!skl) continue;

    let acpPenalty = skl.acp ? actorData.attributes.acp.total : 0;
    let ablMod = actorData.abilities[skl.ability]?.mod || 0;
    let specificSkillBonus = skl.changeBonus || 0;

    // Parse main skills
    let sklValue = skl.rank + (skl.cs && skl.rank > 0 ? 3 : 0) + ablMod + specificSkillBonus - acpPenalty;
    skl.mod = sklValue;

    // Parse sub-skills
    for (const [subSklKey, subSkl] of Object.entries(skl.subSkills || {})) {
      if (!subSkl) continue;
      const subSkill = skl.subSkills?.[subSklKey];
      if (!subSkill) continue;

      acpPenalty = subSkl.acp ? actorData.attributes.acp.total : 0;
      ablMod = actorData.abilities[subSkl.ability]?.mod || 0;
      specificSkillBonus = subSkl.changeBonus || 0;
      sklValue = subSkl.rank + (subSkl.cs && subSkl.rank > 0 ? 3 : 0) + ablMod + specificSkillBonus - acpPenalty;
      subSkill.mod = sklValue;
    }
  }
};

export const getSourceInfo = function (obj, key) {
  if (!obj[key]) {
    obj[key] = { negative: [], positive: [] };
  }
  return obj[key];
};

export const setSourceInfoByName = function (obj, key, name, value, positive = true) {
  const target = positive ? "positive" : "negative";
  const sourceInfo = getSourceInfo(obj, key)[target];
  const data = sourceInfo.find((o) => o.name === name);
  if (data) data.value = value;
  else {
    sourceInfo.push({
      name: name,
      value: value,
    });
  }
};

/**
 * @param {ItemChange[]} changes - An array containing all changes to check. Must be called after they received a value (by ItemChange.applyChange)
 * @param {object} [options]
 * @param {boolean} [options.ignoreTarget] - Whether to only check for modifiers such as enhancement, insight (true) or whether the target (AC, weapon damage) is also important (false)
 * @returns {ItemChange[]} - A list of processed changes, excluding the lower-valued ones inserted (if they don't stack)
 */
export const getHighestChanges = function (changes, options = { ignoreTarget: false }) {
  const highestTemplate = {
    value: 0,
    ids: [],
    highestID: null,
  };
  const highest = Object.keys(CONFIG.FFD20.bonusModifiers).reduce((cur, k) => {
    if (options.ignoreTarget) cur[k] = duplicate(highestTemplate);
    else cur[k] = {};
    return cur;
  }, {});

  for (const c of changes) {
    let h;
    if (options.ignoreTarget) h = highest[c.modifier];
    else h = highest[c.modifier][c.subTarget];

    h.ids.push(c._id);
    if (h.value < c.value || !h.highestID) {
      h.value = c.value;
      h.highestID = c._id;
    }
  }

  {
    let mod, h;
    const filterFunc = function (c) {
      if (h.highestID === c._id) return true;
      if (CONFIG.FFD20.stackingBonusModifiers.indexOf(mod) === -1 && h.ids.includes(c._id)) return false;
      return true;
    };

    for (mod of Object.keys(highest)) {
      if (options.ignoreTarget) {
        h = highest[mod];
        changes = changes.filter(filterFunc);
      } else {
        for (const subTarget of Object.keys(highest[mod])) {
          h = highest[mod][subTarget];
          changes = changes.filter(filterFunc);
        }
      }
    }
  }

  return changes;
};
